export default class WinnersSlider extends Component {
  constructor(props) {
    super(props);
    this.gotoPrev = this.gotoPrev.bind(this);
    this.gotoNext = this.gotoNext.bind(this);
    this.state = {
      nav1: null,
      nav2: null,
      weeks: winners.weeks,
      settings1: {
        slidesToShow: 3,
        className: "slider variable-width custom-bg",
        focusOnSelect: true,
        swipeToSlide: true,
        slidesToScroll: 1,
        dots: false,
        infinite: false,
        variableWidth: true,
      },
      settings2: {
        // slidesToShow: 1,
        swipeToSlide: true,
        dots: false,
        infinite: false,
      },
    };
  }

  componentDidMount() {
    this.setState({
      nav1: this.slider1,
      nav2: this.slider2,
    });
  }

  gotoPrev() {
    this.state.nav1.slickPrev();
    this.state.nav2.slickPrev();
  }

  gotoNext() {
    this.state.nav1.slickNext();
    this.state.nav2.slickNext();
  }

  render() {
    return (
      <div className="workshop-slider-container">
        <div className="ws-icons-slider" style={{ width: "80%" }}>
          <IconButton onClick={this.gotoPrev} className="ws-prev-icon">
            <img src={PrevIcon} alt="workshop-prev" />
          </IconButton>
          <IconButton onClick={this.gotoNext} className="ws-next-icon">
            <img src={PrevIcon} alt="workshop-next" />
          </IconButton>
          <Slider
            className="slider-1"
            asNavFor={this.state.nav2}
            {...this.state.settings1}
            ref={(slider) => (this.slider1 = slider)}
          >
            {this.state.weeks.map((week) => {
              return (
                <div key={week} className="icon-slide-container">
                  <div className="icon-slide">
                    {/* <img src={icon.icon} alt={icon.name} /> */}
                    {/* <icon.icon /> */}
                    <p>Week {week.id}</p>
                  </div>
                </div>
              );
            })}
          </Slider>
        </div>

        <Slider
          className="slider-2 winners-slider-2"
          asNavFor={this.state.nav1}
          {...this.state.settings2}
          ref={(slider) => (this.slider2 = slider)}
        >
          {this.state.weeks.map((week) => {
            return (
              <div key={`week-${week.id}`} className="workshop-slide-container">
                <WinnerCards cards={week.winners} />
              </div>
            );
          })}
        </Slider>
      </div>
    );
  }
}

