import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import { Box, Button, Grid } from "@material-ui/core";
import * as winners from "./winnersObj";
import WinnerPlayButton from "../../public/images/winner_play_button.svg";

const GREEN = "#06bfad";
const PINK = "#e03268";

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    "aria-controls": `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: "100%",
    backgroundColor: "transparent",
  },
  tabpanelStyle: {
    backgroundColor: "#f9f9f9",
  },
  tabsStyle: {
    fontSize: "0.7em",
    fontWeight: "bold",
    color: "#9f9f9f",
    borderRadius: "50px",
    marginLeft: theme.spacing(3.5),
    marginRight: theme.spacing(3.5),
    marginTop: "8px",
    marginBottom: "8px",
    minWidth: 0,
    minHeight: 0,
    // marginBottom: theme.spacing(3),
  },
  selected: {
    backgroundColor: GREEN,
    color: "#ffffff",
    boxShadow: "0px 3px 6px rgba(6,191,173,1)",
  },
  winnerCard: {
    height: "375px",
    maxWidth: "275px",
    boxShadow: "0px 5px 50px rgba(0,0,0,.2)",
    borderRadius: "10px",
    margin: theme.spacing(5),
  },
  winnerImage: {
    height: "1auto",
    maxWidth: "150px",
    borderRadius: "50%",
  },
  playButton: {
    width: "50px",
  },
  innovatorCard: {
    maxWidth: "300px",
    margin: theme.spacing(3),
    backgroundColor: "#ffffff",
    padding: theme.spacing(2),
    borderRadius: "10px",
    borderColor: PINK,
    border: "solid 1px",
  },
  gameLink: {
    "&:hover": {
      boxShadow: "0px 5px 2px rgba(224, 50, 104,.5)",
      transform: "scale(1.1,1.1)",
      backgroundColor: "#ffffff",
    },
  },
  scroller: {
    margin: "auto",
  },
}));

export function TabPanel(props) {
  const { children, value, index, ...other } = props;
  const classes = useStyles();
  const finalStands = winners.winners.weeks[value].winners;
  const winnerCards = (data) => {
    return (
      <Grid container item xs={12} sm={4} className={classes.winnerCard}>
        <Grid
          container
          direction="column"
          alignItems="center"
          justify="space-evenly"
        >
          <Grid item>
            <img alt="" src={data.image} className={classes.winnerImage} />
          </Grid>
          <Grid item>
            <Typography style={{ fontWeight: "bold" }} align="center">
              {data.name}
            </Typography>
            <Typography style={{ fontSize: ".7em" }} align="center">
              Class {data.class}
            </Typography>
            <Typography
              style={{ fontSize: ".7em", maxWidth: "250px" }}
              align="center"
            >
              {data.school}
            </Typography>
          </Grid>
          <Grid item>
            <Button
              className={classes.gameLink}
              href={data.gameLink}
              target="_blank"
            >
              <img alt="" class={classes.playButton} src={WinnerPlayButton} />
            </Button>
          </Grid>
        </Grid>
      </Grid>
    );
  };
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <>
          <Grid container direction="row" alignItems="center" justify="center">
            {finalStands.map((data) => winnerCards(data))}
          </Grid>
        </>
      )}
    </div>
  );
}

export default function WeeklyWinner() {
  const classes = useStyles();
  const innovators = winners.winners.innovators;
  const [value, setValue] = React.useState(0);

  const InnovatorCards = (data) => {
    return (
      <Grid style={{ justifyContent: "center" }} container item xs={12} sm={4}>
        <Grid
          container
          direction="row"
          alignItems="center"
          justify="space-between"
          className={classes.innovatorCard}
        >
          <Grid item>
            <Typography
              style={{ fontWeight: "bold", maxWidth: "150px" }}
              align="left"
            >
              {data.name}
            </Typography>
            <Typography style={{ fontSize: ".7em" }} align="left">
              Class {data.class}
            </Typography>
            <Typography
              style={{ fontSize: ".7em", maxWidth: "150px" }}
              align="left"
            >
              {data.school}
            </Typography>
          </Grid>
          <Grid item>
            <Button
              className={classes.gameLink}
              href={data.gameLink}
              target="_blank"
            >
              <img alt="" class={classes.playButton} src={WinnerPlayButton} />
            </Button>
          </Grid>
        </Grid>
      </Grid>
    );
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <Typography
        align="center"
        style={{ fontWeight: "bold", marginTop: "20px", marginBottom: "50px" }}
        variant="h3"
      >
        WINNERS
      </Typography>
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        style={{ minHeight: 0 }}
      >
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="none"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
          classes={{ scroller: classes.scroller, scrollable: classes.scroller }}
          style={{ minHeight: 0 }}
        >
          <Tab
            className={classes.tabsStyle}
            classes={{ selected: classes.selected }}
            label="Week 1"
            {...a11yProps(0)}
          />
          <Tab
            className={classes.tabsStyle}
            classes={{ selected: classes.selected }}
            label="Week 2"
            {...a11yProps(1)}
          />
          <Tab
            className={classes.tabsStyle}
            classes={{ selected: classes.selected }}
            label="Week 3"
            {...a11yProps(2)}
          />
          <Tab
            className={classes.tabsStyle}
            classes={{ selected: classes.selected }}
            label="Week 4"
            {...a11yProps(3)}
          />
          <Tab
            className={classes.tabsStyle}
            classes={{ selected: classes.selected }}
            label="Week 5"
            {...a11yProps(4)}
          />
          <Tab
            className={classes.tabsStyle}
            classes={{ selected: classes.selected }}
            label="Week 6"
            {...a11yProps(5)}
          />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0} />
      <TabPanel value={value} index={1} />
      <TabPanel value={value} index={2} />
      <TabPanel value={value} index={3} />
      <TabPanel value={value} index={4} />
      <TabPanel value={value} index={5} />
      {/* Innovators */}
      <Typography
        align="center"
        style={{ fontWeight: "bold", marginTop: "20px", marginBottom: "20px" }}
        variant="h3"
      >
        YOUNG INNOVATORS
      </Typography>
      <Grid container direction="row" alignItems="center" justify="center">
        {innovators.map((data) => InnovatorCards(data))}
      </Grid>
    </div>
  );
}
