import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Footer from "../Footer/Footer";
import { Box, Container, Grid, Typography, Button } from "@material-ui/core";
import CodeCarnivalImage from "../../public/images/codecarnival.webp";
import StarBackground from "../../public/images/winner_stars.svg";
import { useSpring, animated } from "react-spring";
import { useInView } from "react-intersection-observer";
import "./WeeklyWinner";
import WinnersSlider from "../WinnersSlider/WinnersSlider";
import "./WinnersComponent.css";
import { winners } from "../Winners/winnersObj";
import WinnerPlayButton from "../../public/images/winner_play_button.svg";

const PINK = "#e03268";

const useStyles = makeStyles((theme) => ({
  root: {
    top: "10vh",
    position: "relative",
    backgroundColor: "#f9f9f9",
    overflowX: "hidden",
  },
  introSection: {
    backgroundImage: `url(${StarBackground})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    backgroundPosition: "center",
    backgroundPositionX: 300,
    minHeight: "90vh",
    marginBottom: theme.spacing(3),
    padding: theme.spacing(3),
  },
  codeCarnivalLogo: {
    height: "300px",
    width: "auto",
    margin: theme.spacing(2),
  },
  carnivalIntroCard: {
    backgroundColor: "#ffffff",
    padding: theme.spacing(4),
    borderRadius: "10px",
    boxShadow: "0px 5px 50px rgba(0,0,0,0.2)",
  },
  numberCap: {
    color: PINK,
    fontWeight: "bold",
    fontSize: "1.1rem",
  },
  numberVal: {
    color: PINK,
    fontWeight: "bold",
    fontSize: "3.7rem",
    marginRight: "3px",
  },
  winnerCard: {
    height: "375px",
    maxWidth: "275px",
    boxShadow: "0px 5px 50px rgba(0,0,0,.2)",
    borderRadius: "10px",
    margin: theme.spacing(5),
  },
  winnerImage: {
    height: "1auto",
    maxWidth: "150px",
    borderRadius: "50%",
  },
  playButton: {
    width: "50px",
  },
  innovatorCard: {
    maxWidth: "300px",
    margin: theme.spacing(3),
    backgroundColor: "#ffffff",
    padding: theme.spacing(2),
    borderRadius: "10px",
    borderColor: PINK,
    border: "solid 1px",
  },
  gameLinkEffect: {
    "&:hover": {
      boxShadow: "0px 3px 6px rgba(224,50,104,.7)",
    },
  },
}));

export default function WinnersComponent() {
  const classes = useStyles();
  const { ref, inView } = useInView({
    threshold: 1,
    triggerOnce: true,
  });
  const props = useSpring({
    config: {
      mass: 200,
      tension: 500,
      friction: 300,
      clamp: true,
    },
    certificate: inView ? 50 : 0,
    participants: inView ? 100 : 0,
    weeks: inView ? 6 : 0,
    from: {
      certificate: 0,
      participants: 0,
      weeks: 0,
    },
  });

  const InnovatorCards = (data) => {
    return (
      <Grid style={{ justifyContent: "center" }} container item xs={12} sm={4}>
        <Grid
          container
          direction="row"
          alignItems="center"
          justify="space-between"
          className={classes.innovatorCard}
        >
          <Grid item>
            <Typography
              style={{ fontWeight: "bold", maxWidth: "150px" }}
              align="left"
            >
              {data.name}
            </Typography>
            <Typography style={{ fontSize: ".7em" }} align="left">
              Class {data.class}
            </Typography>
            <Typography
              style={{ fontSize: ".7em", maxWidth: "150px" }}
              align="left"
            >
              {data.school}
            </Typography>
          </Grid>
          <Grid item>
            <Button
              className={classes.gameLinkEffect}
              href={data.gameLink}
              target="_blank"
            >
              <img
                alt=""
                className={classes.playButton}
                src={WinnerPlayButton}
              />
            </Button>
          </Grid>
        </Grid>
      </Grid>
    );
  };

  return (
    <div className={classes.root}>
      <Container className="container-winners-component">
        {/* Intro Section */}
        <Grid
          container
          direction="row"
          alignItems="center"
          justify="center"
          className={classes.introSection}
        >
          <img
            alt="CodeCarnival"
            className={classes.codeCarnivalLogo}
            src={CodeCarnivalImage}
          />
          <Grid alignContent="center" container item sm={6} xs={12}>
            <Grid item>
              <div className={classes.carnivalIntroCard}>
                <Typography
                  variant="h3"
                  style={{ fontWeight: "bold", color: PINK }}
                  className="hero-heading"
                >
                  CODE CARNIVAL
                </Typography>
                <Typography style={{ fontWeight: "bold", color: "#666666" }}>
                  Robofreaks presents “CodeCarnival”, a 6 week long Scratch
                  Coding Competition, Learn to create games, have fun competing
                  and win amazing prizes!
                </Typography>
              </div>
            </Grid>
          </Grid>
          {/* Some Numbers Here */}
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
            spacing={4}
            ref={ref}
            style={{ marginTop: "50px" }}
          >
            <Grid item sm={4} xs={12}>
              <Box
                display="flex"
                flexWrap="wrap"
                alignItems="center"
                justifyContent="center"
              >
                <Typography className={classes.numberVal}>
                  <animated.span>
                    {props.participants.interpolate((val) => Math.floor(val))}
                  </animated.span>
                  +
                </Typography>
                <Typography className={classes.numberCap}>
                  Students
                  <br />
                  Participated
                </Typography>
              </Box>
            </Grid>
            <Grid item sm={4} xs={12}>
              <Box
                display="flex"
                flexWrap="wrap"
                alignItems="center"
                justifyContent="center"
              >
                <Typography className={classes.numberVal}>
                  <animated.span>
                    {props.certificate.interpolate((val) => Math.floor(val))}
                  </animated.span>
                  +
                </Typography>
                <Typography className={classes.numberCap}>
                  Certificates Given
                </Typography>
              </Box>
            </Grid>
            <Grid item sm={4} xs={12}>
              <Box
                display="flex"
                flexWrap="wrap"
                alignItems="center"
                justifyContent="center"
              >
                <Typography className={classes.numberVal}>
                  <animated.span>
                    {props.weeks.interpolate((val) => Math.floor(val))}
                  </animated.span>
                </Typography>
                <Typography className={classes.numberCap}>
                  Total Weeks
                </Typography>
              </Box>
            </Grid>
          </Grid>
        </Grid>
        {/* Winners Section */}
        {/* <WeeklyWinner /> */}
        <WinnersSlider />
        <Typography
          align="center"
          style={{
            fontWeight: "bold",
            marginTop: "20px",
            marginBottom: "20px",
          }}
          variant="h4"
        >
          YOUNG INNOVATORS
        </Typography>
        <Grid container direction="row" alignItems="center" justify="center">
          {winners.innovators.map((data) => InnovatorCards(data))}
        </Grid>
      </Container>
      <Footer />
    </div>
  );
}
